/* modules */

.stub {
  height: 100%;
  width: 100%;
  position: relative;
  overflow: hidden;
  color: $color-white;
  background: {
    color: #822ece;
    image: url('../img/stub/bg.jpg');
    position: center center;
    repeat: no-repeat;
    size: cover; }
  &__phone {
    position: absolute;
    z-index: 5;
    right: 125px;
    bottom: 0; }
  &__content {
    height: 100%;
    display: flex;
    align-items: center; }
  &__footer {
    position: absolute;
    z-index: 10;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(#21155d,.25);
    height: 80px;
    align-items: center;
    display: flex;
    font-size: 16px;
    a {
      color: $color-white;
      text-decoration: underline;
      &:hover, &:focus {
        text-decoration: none; } }
    ul {
      margin: 0;
      padding: 0;
      display: flex;
      justify-content: space-between;
      li {
        margin: 0;
        list-style: none; } } }
  &__logo {
    padding-left: 144px;
    position: relative;
    height: 103px;
    margin-bottom: 32px;
    z-index: 15;
    &::before {
      content: 'Retouch Body';
      font-size: 203px;
      font-family: $font-roboto-bold;
      line-height: 0.8;
      position: absolute;
      left: -12px;
      bottom: 0;
      color: rgba($color-white,.05);
      white-space: nowrap; }
    strong {
      display: block;
      line-height: 0.8;
      font-size: 67px;
      margin-bottom: 16px; }
    span {
      display: block;
      line-height: 0.8;
      font-size: 42px; }
    svg {
      position: absolute;
      width: 98px;
      height: 103px;
      left: 0;
      top: 0;
      g {
        fill: $color-white; } } }
  p {
    line-height: 23px;
    margin-bottom: 30px; }
  &__app {
    display: flex;
    position: relative;
    z-index: 20;
    a {
      display: flex;
      width: 200px;
      height: 65px;
      border-radius: 50px;
      background: $color-black;
      justify-content: center;
      align-items: center;
      color: $color-white;
      text-decoration: none;
      box-shadow: 0 10px 36px rgba(#bed5ff,.5);
      transition: box-shadow 0.35s;
      &:hover, &:focus {
        box-shadow: 0 0px 36px rgba(#bed5ff,.5); }
      &[disabled] {
        opacity: 0.5;
        cursor: default;
        &:hover, &:focus {
          box-shadow: 0 10px 36px rgba(#bed5ff,.5); } } }
    &>div {
      &+div {
        padding-left: 55px; } } } }

.section {
  &__title {
    color: $color-white;
    position: relative;
    font-size: 48px;
    font-family: $font-roboto-bold;
    line-height: 1.1;
    margin-bottom: 20px;
    &--1 {
      &::before {
        content: attr(data-shadow);
        font-size: 203px;
        font-family: $font-roboto-bold;
        line-height: 0.8;
        position: absolute;
        left: -12px;
        bottom: 6px;
        color: rgba($color-white,.05);
        white-space: nowrap; } }
    &--4 {
      color: $primary-color; }
    &--5, &--13 {
      color: $primary-color; }
    &--6 {
      color: $primary-color; }
    &--7, &--8 {
      text-align: center;
      color: $primary-color;
      margin-bottom: 55px; }
    &--dark {
      color: $primary-color; }
    &--11 {
      font-size: 34px;
      .fa {
        display: none; } } }
  &1 {
    background: {
      position: top center;
      repeat: no-repeat;
      image: url('../img/bg/bg-section-1.png'); }
    &__img {
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translateY(-37%) translateX(65px);
      &--320 {
        display: none; } }
    &__content {
      position: absolute;
      left: 0;
      transform: translateY(-50%) translateX(-195px);
      p {
        max-width: 570px;
        margin-bottom: 25px; } }
    &__app {
      display: flex;
      position: relative;
      z-index: 20;
      a {
        display: flex;
        width: 200px;
        height: 65px;
        border-radius: 50px;
        background: $color-black;
        justify-content: center;
        align-items: center;
        color: $color-white;
        text-decoration: none;
        box-shadow: 0 10px 36px rgba(#bed5ff,.5);
        transition: box-shadow 0.35s;
        &:hover, &:focus {
          box-shadow: 0 0px 36px rgba(#bed5ff,.5); }
        &[disabled] {
          opacity: 0.5;
          cursor: default;
          &:hover, &:focus {
            box-shadow: 0 10px 36px rgba(#bed5ff,.5); } } }
      &>div {
        &+div {
          padding-left: 55px; } } }
