      background-size: 32px 50px; } }
  &2, &3, &10 {
    background: {
      position: top center;
      size: cover;
      repeat: no-repeat;
      image: url('../img/bg/bg-section-2.jpg'); }
    &__wrapper {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      display: flex;
      &>div {
        position: relative;
        width: 33.3%;
        flex-grow: 1;
        transition: background 0.35s;
        background: {
          color: rgba(#190e27,.65);
          position: center center;
          repeat: no-repeat;
          size: cover;
          image: none; }
        overflow: hidden;
        &:nth-child(2) {
