    &__block {
      color: $color-white;
      position: absolute;
      left: 70px;
      right: 70px;
      bottom: 150px;
      font-size: 16px; }
    &__btn {
      position: absolute;
      z-index: 10;
      left: 50%;
      transform: translateX(-50%);
      bottom: 30px;
      border: 1px solid $color-white;
      color: $color-white;
      text-decoration: none;
      height: 65px;
      line-height: 61px;
      width: 200px;
      text-align: center;
      border-radius: 35px;
      background: rgba($color-white,.12);
      &:hover, &:focus {
        color: $color-white;
        background: rgba($color-white,.5); } }
    &__more {
      color: $color-white;
      text-decoration: none;
      font-family: $font-roboto-medium;
      text-shadow: 0 0 0 #bed5ff;
      .fa {
        margin-left: 5px; }
      &:hover, &:focus {
        color: $color-white;
        text-shadow: 0 5px 16px #bed5ff; } } }
