  &4, &6 {
    background: $color-white;
    color: $primary-color;
    &__content {
      transform: translateY(-50%) translateX(113px);
      position: absolute;
      left: 50%;
      max-width: 590px;
      p {
        &:last-child {
          margin-bottom: 0; } } } }
  &4 {
    &__slider {
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      width: 50%;
      background: #e9f6f9;
      &-wrapper {
        height: 100vh;
        .slick-list, .slick-track, .slick-slide {
          height: 100%;
          &:focus {
            outline: none; } } }
      &-block {
        height: 100%;
        display: flex;
        flex-direction: column;
        &>div {
          height: 50%;
          position: relative;
          img {
            height: 100%;
            width: 100%;
            object-fit: cover; } } }
      &-bottom {
        &::after {
          position: absolute;
          top: 0;
          right: 0;
          z-index: 2;
          bottom: 0;
          left: 0;
          opacity: 0.5;
          background: $color-white;
          content: ''; } }
      &-btn {
        height: 45px;
        width: 150px;
        border-radius: 3px;
        background: $color-eee;
        color: $primary-color;
        line-height: 45px;
        text-align: center;
        position: absolute;
        z-index: 5;
        right: 30px;
        bottom: 30px;
        font-size: 14px;
        &--top {
          bottom: inherit;
          top: 30px; } }
      .slick-dots {
        bottom: 20px;
        li {
          height: 11px;
          width: 11px;
          margin: 0 10px;
          button {
            height: 10px;
            width: 10px;
            font-size: 0;
            border: 1px solid $color-white;
            background: $color-white;
            border-radius: 50%;
            &::before {
              display: none; } }
          &.slick-active {
            button {
              background: transparent; } } } } } }
  &5 {
    background: $color-white;
    color: $primary-color;
    &__content {
      transform: translateY(-50%) translateX(-217px);
      position: absolute;
      left: 0;
      max-width: 640px;
      p {
        &:last-child {
          margin-bottom: 0; } } }
    &__img {
      position: absolute;
      width: 50%;
      top: 0;
      right: 0;
      bottom: 0;
      background: $color-eee;
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        margin-left: 60px; } } }
  &6 {
    &__wrapper {
      position: absolute;
      left: 0;
      width: 50%;
      top: 0;
      bottom: 0;
      display: flex;
      flex-wrap: wrap;
      &>div {
        width: 50%;
        height: 50%; } }
    &__block {
      height: 100%;
      position: relative;
      &-img {
        width: 100%;
        height: 100%;
        object-fit: cover; }
      &-title {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        background: rgba($color-white,.85);
        min-height: 60px;
        font-size: 20px;
        padding: 15px 10px;
        font-family: $font-roboto-bold;
        text-align: center;
        z-index: 10;
        transition: all 0.35s;
        svg {
          width: 30px;
          height: 30px;
          display: inline-block;
          vertical-align: -8px;
          margin-right: 10px;
          path {
            fill: #444444;
            transition: fill 0.35s; } } }
      &-text {
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        background: rgba($color-black,.6);
        color: $color-white;
        font-size: 16px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-family: $font-roboto-light;
        z-index: 5;
        opacity: 0;
        visibility: hidden;
        transition: all 0.35s;
        &>div {
          max-width: 300px; } }
      &:hover {
        .section6__block {
          &-title {
            background: transparent;
            color: $color-white;
            svg {
              path {
                fill: $color-white; } } }
          &-text {
            opacity: 1;
            visibility: visible; } } } } }
  &7 {
    background: $color-eee;
    .section__container {
      height: auto;
      background: none; }
    &__slider {
      background: $color-white;
      &-block {
        color: $primary-color;
        font-size: 16px;
        padding: 35px 20px 20px 20px;
        height: 100%;
        p {
          // max-height: 90px
          overflow: hidden; } }
      &-title {
        font-size: 24px;
        font-family: $font-roboto-bold;
        margin-bottom: 10px; }
      &-time {
        font-size: 14px;
        white-space: nowrap;
        i {
          display: inline-block;
          vertical-align: 1px;
          margin-right: 5px; } }
      .slick {
        &-slide {
          &:focus {
            outline: none; } }
        &-dots {
          bottom: -40px;
          li {
            height: 11px;
            width: 11px;
            margin: 0 10px;
            button {
              height: 10px;
              width: 10px;
              font-size: 0;
              border: 1px solid $primary-color;
              background: $primary-color;
              border-radius: 50%;
              &::before {
                display: none; } }
            &.slick-active {
              button {
                background: transparent; } } } } } } }
  &8 {
    background: $color-white;
    &--gray {
      background: $color-eee; }
    .section__container {
      height: auto;
      background: none; }
    &__row {
      display: flex;
      margin: 0 -15px 50px -15px;
      &>div {
        padding: 0 15px;
        width: 33.3%; }
      &--carousel {
        display: block;
        margin-left: 0;
        margin-right: 0;
        &>div {
          padding: 0;
          width: auto; }
        .slick-slide {
          padding: 1px 15px;
          &:focus {
            outline: none; } } } }
    &__block {
      color: $primary-color;
      box-shadow: 0 0 0 1px $color-eee;
      font-size: 16px;
