        opacity: 0.5;
        cursor: default;
        &:hover, &:focus {
          box-shadow: 0 10px 36px rgba(#bed5ff,.5); } } }
    div {
      &+div {
        margin-left: 60px; } } }
  &--category {
    position: absolute;
    left: 0;
    bottom: 0;
    right: 375px;
    color: $color-white;
    background: rgba(#021326,.9);
    z-index: 20;
    .download {
      &__title {
